import React, { Component } from "react";
import DriverRequestRow from "./DriverRequestRow";

class DriverRequestsTable extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const driverRequestPackages = this.props.DriverRequestPackages;
        return <section>
            <div class="table-responsive back-color-gray">
                <table class="table table-striped">
                    <thead>
                        <tr>
                            <th>DRIVER</th>
                            <th>REQUESTED ON</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {driverRequestPackages.map((driverRequestPackage, index) => {
                            return <DriverRequestRow key={index} Index={index} DriverRequestPackage={driverRequestPackage} />;
                        })}
                    </tbody>
                </table>
            </div>
        </section>;
    }
};

export default DriverRequestsTable;